import React from 'react';
import Helmet from 'react-helmet';
import { TITLE } from '../../constants';
import { MainTitle, ToolTip } from './styled';

const Main = () => {
  return (
    <>
      <Helmet>
        <title>{TITLE}</title>
        <meta name="og:title" content={TITLE} />
      </Helmet>
      <MainTitle>
        <h1>JIGGLOG</h1>
        <h3>JIGGLOG   </h3>
      </MainTitle>
      <ToolTip id="tooltip">
        <h1></h1>
        <h2>        .</h2>
      </ToolTip>
    </>
  );
};

export default Main;
